
.Sidebar ul{
  overflow-y: auto;
    display: block;
    margin: auto;
    border: 1px solid #222;
    width: 80%;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
form {
  margin: auto;
  top: 0;
  left: 0;
  margin-top: 30%;
  background-color: whitesmoke;
}
form input{
  width: 90%;
  border: none;
  border: 1px solid #444;
  border-radius: 10px;
  padding-left: 10px;
  height: 30px;
}
.btn {background: linear-gradient(gold,orange);
  font-family: cursive;
  font-size: medium;
  cursor: pointer;
  border-radius: 10px;
  margin-left: 1%;
}

#heremsg {
  position: relative;
  display: table;
  width: 80%;
  height: 100px;
  max-height: 10px;
  overflow-y: auto;
  z-index: +1;
}

.ds {
  overflow-y: auto;
  max-height: 200px;
}